import React from 'react'
import Box from '@mui/material/Box'
import Drawer from '@mui/material/Drawer'
import Button from '@mui/material/Button'
import List from '@mui/material/List'
import ListItem from '@mui/material/ListItem'
import ListItemIcon from '@mui/material/ListItemIcon'
import ListItemText from '@mui/material/ListItemText'
import SettingsIcon from '@mui/icons-material/Settings'
import ListIcon from '@mui/icons-material/List'
import ImportIcon from '@mui/icons-material/ArrowUpward'
import ExportIcon from '@mui/icons-material/ArrowDownward'
import SettingsModal, { ItemEntry } from './SettingsModal'
import {
	Dialog,
	DialogTitle,
	DialogContent,
	DialogActions,
	TextField,
	Select,
	MenuItem,
	InputLabel,
	FormControl,
	Checkbox
} from '@mui/material'
import dataCenters from '../servers.json'
import { getItemsForExport, itemsToItemEntry } from '../utils/itemutils'

interface ISettingsProps {
	rows: ItemEntry[]
	datacenter: string
	saveids: (ids: ItemEntry[], callback?: () => void) => void
	changeDatacenter: (dataCenter: string) => void
	homeServer: string
	serverList: string[]
	changeHomeServer: (homeServer: string) => void
	autoRefresh: boolean
	setAutoRefresh: (autoRefresh: boolean) => void
}

const datacenters = dataCenters.map((dc) => dc.name)

const Settings = ({
	rows,
	datacenter,
	saveids,
	changeDatacenter,
	homeServer,
	serverList,
	changeHomeServer,
	autoRefresh,
	setAutoRefresh
}: ISettingsProps) => {
	const importRef = React.useRef<typeof TextField>()
	const [isDrawerOpened, setDrawerOpened] = React.useState(false)
	const [isModalOpened, setModalOpened] = React.useState(false)
	const [isImportDialogOpened, setImportDialogOpened] = React.useState(false)
	const [isExportDialogOpened, setExportDialogOpened] = React.useState(false)
	const [key, setKey] = React.useState(0)

	const toggleDrawer = (open: boolean) => (event: React.KeyboardEvent | React.MouseEvent) => {
		if (
			event.type === 'keydown' &&
			((event as React.KeyboardEvent).key === 'Tab' ||
				(event as React.KeyboardEvent).key === 'Shift')
		) {
			return
		}
		setDrawerOpened(open)
	}

	const handleImportDialogClose = () => {
		setImportDialogOpened(false)
	}

	const handleExportDialogClose = () => {
		setExportDialogOpened(false)
	}

	const convertStringToItemEntry = () => {
		if (importRef?.current) {
			const input = importRef.current as unknown as HTMLInputElement
			itemsToItemEntry(
				input.value,
				(items) =>
					saveids(items, () => {
						setKey(key + 1)
					}),
				() => {
					handleImportDialogClose()
					setDrawerOpened(false)
				}
			)
		}
	}

	return (
		<div>
			<Button style={{ float: 'left' }} onClick={toggleDrawer(true)}>
				<SettingsIcon color='secondary' />
			</Button>
			<div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
				<SettingsModal
					rows={rows}
					key={key}
					open={isModalOpened}
					setOpen={setModalOpened}
					saveids={saveids}
				/>
			</div>
			<div>
				<Dialog open={isImportDialogOpened} onClose={handleImportDialogClose}>
					<DialogTitle>Import</DialogTitle>
					<DialogContent>
						<TextField
							inputRef={importRef}
							autoFocus
							margin='dense'
							type='text'
							fullWidth
							variant='standard'
						></TextField>
					</DialogContent>
					<DialogActions>
						<Button onClick={handleImportDialogClose}>Close</Button>
						<Button onClick={() => convertStringToItemEntry()}>Import</Button>
					</DialogActions>
				</Dialog>
			</div>
			<div>
				<Dialog open={isExportDialogOpened} onClose={handleExportDialogClose}>
					<DialogTitle>Export</DialogTitle>
					<DialogContent>
						<TextField
							autoFocus
							margin='dense'
							type='text'
							fullWidth
							variant='standard'
							value={getItemsForExport(rows)}
						></TextField>
					</DialogContent>
					<DialogActions>
						<Button onClick={handleExportDialogClose}>Close</Button>
					</DialogActions>
				</Dialog>
			</div>

			<Drawer anchor='left' open={isDrawerOpened} onClose={toggleDrawer(false)}>
				<Box sx={{ width: 300 }} role='presentation'>
					<List>
						<ListItem>
							<FormControl fullWidth>
								<InputLabel id='datacenter-label'>Datacenter</InputLabel>
								<Select
									labelId='datacenter-label'
									value={datacenter}
									label='datacenter'
									onChange={(v) => changeDatacenter(v.target.value)}
								>
									{datacenters.map((dc) => {
										return (
											<MenuItem key={dc} value={dc}>
												{dc}
											</MenuItem>
										)
									})}
								</Select>
							</FormControl>
						</ListItem>
						<ListItem>
							<FormControl fullWidth>
								<InputLabel id='homeserver-label'>Home Server</InputLabel>
								<Select
									labelId='homeserver-label'
									value={homeServer}
									label='homeserver'
									onChange={(v) => changeHomeServer(v.target.value)}
								>
									{serverList.map((s) => {
										return (
											<MenuItem key={s} value={s}>
												{s}
											</MenuItem>
										)
									})}
								</Select>
							</FormControl>
						</ListItem>
						<ListItem
							button
							onClick={() => {
								setModalOpened(true)
							}}
						>
							<ListItemIcon>
								<ListIcon />
							</ListItemIcon>
							<ListItemText primary={'Item List'} />
						</ListItem>
						<ListItem
							button
							onClick={() => {
								setImportDialogOpened(true)
							}}
						>
							<ListItemIcon>
								<ImportIcon />
							</ListItemIcon>
							<ListItemText primary={'Import Item List'} />
						</ListItem>
						<ListItem
							button
							onClick={() => {
								setExportDialogOpened(true)
							}}
						>
							<ListItemIcon>
								<ExportIcon />
							</ListItemIcon>
							<ListItemText primary={'Export Item List'} />
						</ListItem>
						<ListItem>
							<ListItemIcon>
								<Checkbox
									onChange={(e) => setAutoRefresh(e.currentTarget.checked)}
									checked={autoRefresh}
									color='success'
								/>
							</ListItemIcon>
							<ListItemText primary={'Auto refresh'} />
						</ListItem>
					</List>
				</Box>
			</Drawer>
		</div>
	)
}

export default Settings
