import axios from 'axios'

export interface MarketBoardItemsListing {
	lastReviewTime: number
	pricePerUnit: number
	quantity: number
	stainID: number
	worldName: string
	worldID: number
	creatorName: string
	creatorID: string
	hq: boolean
	isCrafted: boolean
	listingID: string
	materia: [
		{
			slotID: number
			materiaID: number
		}
	]
	onMannequin: true
	retainerCity: number
	retainerID: string
	retainerName: string
	sellerID: string
	total: number
}

export interface MarketBoardItems {
	itemID: number
	worldID: number
	lastUploadTime: number
	listings: MarketBoardItemsListing[]
	recentHistory: [
		{
			hq: boolean
			pricePerUnit: number
			quantity: number
			timestamp: number
			worldName: string
			worldID: number
			buyerName: string
			total: number
		}
	]
	dcName: string
	currentAveragePrice: number
	currentAveragePriceNQ: number
	currentAveragePriceHQ: number
	regularSaleVelocity: number
	nqSaleVelocity: number
	hqSaleVelocity: number
	averagePrice: number
	averagePriceNQ: number
	averagePriceHQ: number
	minPrice: number
	minPriceNQ: number
	minPriceHQ: number
	maxPrice: number
	maxPriceNQ: number
	maxPriceHQ: number
	stackSizeHistogram: {
		additionalProp1: number
		additionalProp2: number
		additionalProp3: number
	}
	stackSizeHistogramNQ: {
		additionalProp1: number
		additionalProp2: number
		additionalProp3: number
	}
	stackSizeHistogramHQ: {
		additionalProp1: number
		additionalProp2: number
		additionalProp3: number
	}
	worldName: string
	worldUploadTimes: {
		additionalProp1: number
		additionalProp2: number
		additionalProp3: number
	}
}

export interface MarketBoardListings {
	itemIDs: number[]
	items: MarketBoardItems[]
	worldID: number
	dcName: string
	unresolvedItems: number[]
	worldName: string
}

export const dataIsMarketBoardItems = (
	data: MarketBoardListings | MarketBoardItems
): data is MarketBoardItems => {
	return (<MarketBoardItems>data).listings !== undefined
}

export const getItems = (datacenter: string, listOfIds: number[]) => {
	return axios.get<MarketBoardListings | MarketBoardItems>(
		`https://universalis.app/api/${datacenter}/${listOfIds.join(',')}`
	)
}
