import { v4 as uuidv4 } from 'uuid'
import { ItemEntry } from '../settings/SettingsModal'

export function itemsToItemEntry(
	input: string,
	saveCallback?: (items: ItemEntry[]) => void,
	endCallback?: () => void
) {
	const matches = input.match(/i:\d+;*/g)?.map((x) => x.replace('i:', ''))
	const items = matches?.map<ItemEntry>((x) => {
		return {
			id: uuidv4(),
			value: parseInt(x)
		}
	})
	if (items) {
		saveCallback?.(items)
	}

	endCallback?.()
	return items
}

export function getItemsForExport(rows: ItemEntry[]) {
	return rows.map((r) => `i:${r.value}`).join(';')
}
