import * as React from 'react'
import './App.css'
import Settings from './settings/Settings'
import { ThemeProvider, createTheme } from '@mui/material/styles'
import { v4 as uuidv4 } from 'uuid'
import MainDataGrid, { DataGridEntry } from './datagrid/MainDataGrid'
import { ItemEntry } from './settings/SettingsModal'
import { IconButton } from '@mui/material'
import RefreshIcon from '@mui/icons-material/Refresh'
import { getItems } from './api/universalis'
import dgeprocessor from './processors/dgeprocessor'
import dataCenters from './servers.json'
import { getItemsForExport, itemsToItemEntry } from './utils/itemutils'

const darkTheme = createTheme({
	palette: {
		mode: 'dark'
	}
})

interface IAppState {
	ids: ItemEntry[]
	data: DataGridEntry[]
	isLoading: boolean
	dataCenter: string
	homeServer: string
	serverList: string[]
	autoRefresh: boolean
}
export default class App extends React.Component<Record<string, never>, IAppState> {
	timeout?: NodeJS.Timeout
	timeoutTime = 1 * 60 * 1000
	constructor(props: Record<string, never>) {
		super(props)

		const chaos = dataCenters.find((dc) => dc.name === 'Chaos')
		if (chaos === undefined) throw new Error('Chaos not found')
		const ragnarok = chaos.servers.find((s) => s === 'Ragnarok')
		if (ragnarok === undefined) throw new Error('Ragnarok not found')

		const localIds = localStorage.getItem('ids')
		let items: ItemEntry[] | undefined

		if (localIds !== null) {
			items = itemsToItemEntry(localIds)
		}

		this.state = {
			ids: items || [
				{ id: uuidv4(), value: 7046 },
				{ id: uuidv4(), value: 7028 }
			],
			data: [],
			isLoading: false,
			dataCenter: chaos?.name,
			homeServer: ragnarok,
			serverList: chaos.servers,
			autoRefresh: false
		}
	}

	updateData(newData: DataGridEntry | undefined) {
		if (this.state.isLoading) this.setState({ isLoading: false })
		if (newData)
			this.setState((state) => {
				return {
					data: [...state.data, newData]
				}
			})
	}

	refresh() {
		this.setState(
			{
				data: [],
				isLoading: true
			},
			() => {
				if (this.state.isLoading) {
					getItems(
						this.state.dataCenter,
						this.state.ids.map((id) => id.value).filter((v) => v !== -1)
					).then(async (response) => {
						await dgeprocessor(response.data, this.updateData.bind(this), this.state.homeServer)
					})
				}
			}
		)
	}

	autoRefresh() {
		this.refresh()
		this.timeout = setTimeout(this.autoRefresh.bind(this), this.timeoutTime)
	}

	componentDidUpdate(prevProps: Record<string, never>, prevState: IAppState) {
		if (!prevState.autoRefresh && this.state.autoRefresh) {
			this.autoRefresh()
		} else if (prevState.autoRefresh && !this.state.autoRefresh && this.timeout !== undefined) {
			clearTimeout(this.timeout)
			this.timeout = undefined
		}
	}

	saveids(ids: ItemEntry[], callback?: () => void) {
		this.setState({ ids }, callback)
		localStorage.setItem('ids', getItemsForExport(ids))
	}

	changeDatacenter(dataCenter: string) {
		const serverList = dataCenters.find((m) => m.name === dataCenter)?.servers
		if (serverList) this.setState({ dataCenter, homeServer: serverList[0], serverList })
	}

	changeHomeServer(homeServer: string) {
		this.setState({ homeServer })
	}

	setAutoRefresh(autoRefresh: boolean) {
		this.setState({ autoRefresh })
	}

	render() {
		return (
			<ThemeProvider theme={darkTheme}>
				<div className='App'>
					<header style={{ display: 'flex' }} className='App-header'>
						<div style={{ display: 'flex', flex: 1 }}>
							<div style={{ display: 'flex', flexDirection: 'column' }}>
								<Settings
									rows={this.state.ids}
									datacenter={this.state.dataCenter}
									saveids={this.saveids.bind(this)}
									changeDatacenter={this.changeDatacenter.bind(this)}
									homeServer={this.state.homeServer}
									serverList={this.state.serverList}
									changeHomeServer={this.changeHomeServer.bind(this)}
									autoRefresh={this.state.autoRefresh}
									setAutoRefresh={this.setAutoRefresh.bind(this)}
								/>
								<IconButton onClick={this.refresh.bind(this)}>
									<RefreshIcon color='secondary' />
								</IconButton>
							</div>

							<MainDataGrid
								style={{ flex: 1 }}
								data={this.state.data}
								isLoading={this.state.isLoading}
							/>
						</div>
					</header>
				</div>
			</ThemeProvider>
		)
	}
}
