// eslint-disable-next-line import/named
import { DataGrid, GridColumns } from '@mui/x-data-grid'
import React from 'react'

export interface DataGridEntry {
	id: number
	itemName?: string
	buyFrom: string
	itemBuyPrice: number
	quantity: number
	sellTo: string
	itemSellPrice: number
	profitPerItem: number
}

export default function MainDataGrid({
	data,
	style,
	isLoading
}: {
	data: DataGridEntry[]
	style?: React.CSSProperties
	isLoading: boolean
}) {
	const columns: unknown[] = [
		// { field: 'id', headerName: 'ID', width: 0 },
		{ field: 'itemName', headerName: 'Item Name' },
		{ field: 'buyFrom', headerName: 'Buy From' },
		{ field: 'itemBuyPrice', headerName: 'Buy Price' },
		// { field: 'sellTo', headerName: 'Sell To' },
		{ field: 'itemSellPrice', headerName: 'Sell Price' },
		{ field: 'profitPerItem', headerName: 'Profit' }
	]

	return (
		<div style={style}>
			<DataGrid rows={data} columns={columns as GridColumns<DataGridEntry>} loading={isLoading} />
		</div>
	)
}
