import React from 'react'
import { Modal, Box, Button, IconButton } from '@mui/material'
// eslint-disable-next-line import/named
import { DataGrid, GridColumns, GridSelectionModel } from '@mui/x-data-grid'
import DeleteIcon from '@mui/icons-material/Delete'
import AddIcon from '@mui/icons-material/Add'
import { v4 as uuidv4 } from 'uuid'

export interface ItemEntry {
	id: string
	value: number
}

const style = {
	position: 'absolute' as const,
	top: '50%',
	left: '50%',
	transform: 'translate(-50%, -50%)',
	width: 400
}

export interface ISettingsModal {
	rows: ItemEntry[]
	open: boolean
	setOpen: React.Dispatch<React.SetStateAction<boolean>>
	saveids: (ids: ItemEntry[]) => void
}

const SettingsModal = ({ rows, open, setOpen, saveids }: ISettingsModal) => {
	const initialState = rows.slice()
	const [localRows, setRows] = React.useState(rows.slice())
	const [selectionModel, setSelectionModel] = React.useState<GridSelectionModel>()
	const handleClose = () => setOpen(false)
	const columns: unknown[] = [
		{ field: 'id', headerName: 'ID', width: 0, editable: false, hide: true },
		{ field: 'value', headerName: 'Item Id', width: 70, editable: true },
		{
			field: 'delete',
			width: 60,
			sortable: false,
			disableColumnMenu: true,
			editable: true,
			renderHeader: () => {
				return (
					<IconButton
						onClick={() => {
							const selectedIDs = new Set(selectionModel)
							setRows(localRows.filter((x) => !selectedIDs.has(x.id)))
						}}
					>
						<DeleteIcon />
					</IconButton>
				)
			}
		},
		{
			field: 'add',
			width: 60,
			sortable: false,
			disableColumnMenu: true,
			editable: true,
			renderHeader: () => {
				return (
					<IconButton
						onClick={() => {
							setRows([{ id: uuidv4(), value: -1 }, ...localRows])
						}}
					>
						<AddIcon />
					</IconButton>
				)
			}
		}
	]
	return (
		<Modal
			open={open}
			onClose={() => {
				setRows(initialState)
				handleClose()
			}}
		>
			<Box style={style}>
				<div style={{ height: 400, width: '100%' }}>
					<DataGrid
						rows={localRows}
						columns={columns as GridColumns<ItemEntry>}
						pageSize={5}
						rowsPerPageOptions={[5]}
						checkboxSelection
						onSelectionModelChange={(ids) => {
							setSelectionModel(ids)
						}}
						onCellEditCommit={(p) => {
							const row = localRows.find((lr) => lr.id == p.id)
							if (row) {
								row.value = p.value
								setRows([...localRows])
							}
						}}
					/>
				</div>
				<Button
					onClick={() => {
						setRows(initialState)
						handleClose()
					}}
				>
					Cancel
				</Button>
				<Button
					style={{ float: 'right' }}
					onClick={() => {
						saveids(localRows)
						handleClose()
					}}
				>
					Save
				</Button>
			</Box>
		</Modal>
	)
}

export default SettingsModal
