import { MarketBoardListings, MarketBoardItems, dataIsMarketBoardItems } from '../api/universalis'
import { getItemName } from '../api/xivapi'
import { DataGridEntry } from '../datagrid/MainDataGrid'

const marketBoardItemsProcessor = async (
	data: MarketBoardItems,
	homeServer: string
): Promise<DataGridEntry | undefined> => {
	const listings = data.listings

	if (listings.length === 0) return undefined

	const cheapest = listings[0]

	const homeListings = listings.filter((l) => l.worldName === homeServer)

	const cheapestAtHome = homeListings.at(0)
	if (cheapestAtHome === undefined) return undefined

	const name = await getItemName(data.itemID)
	return {
		id: data.itemID,
		itemName: name.data.Name,
		buyFrom: cheapest.worldName,
		quantity: cheapest.quantity,
		itemBuyPrice: cheapest.pricePerUnit,
		sellTo: homeServer,
		itemSellPrice: cheapestAtHome.pricePerUnit,
		profitPerItem: cheapestAtHome.pricePerUnit - cheapest.pricePerUnit
	}
}

const marketBoardListingProcessor = async (
	data: MarketBoardListings,
	updateData: (data: DataGridEntry | undefined) => void,
	homeServer: string
): Promise<void> => {
	for (let i = 0; i < data.items.length; i++) {
		updateData(await marketBoardItemsProcessor(data.items[i], homeServer))
	}
}

const dgeprocessor = async (
	data: MarketBoardListings | MarketBoardItems,
	updateData: (data: DataGridEntry | undefined) => void,
	homeServer: string
): Promise<void> => {
	if (dataIsMarketBoardItems(data)) {
		updateData(await marketBoardItemsProcessor(data, homeServer))
		return
	}

	marketBoardListingProcessor(data, updateData, homeServer)
}

export default dgeprocessor
